<template>
  <div class="page">
    <header>
      <Topbar/>
    </header>
    <div class="main">
      <Editor/>
      <Preview/>
    </div>
  </div>
</template>

<script>
import 'normalize.css/normalize.css'
import './assets/reset.css'

import Topbar from './components/Topbar'
import Editor from './components/Editor'
import Preview from './components/Preview'
import icons from './assets/icons'

export default {
  name: 'app',
  data: function(){
    return {
      text: 'good'
    }
  },
  components: {Topbar, Editor, Preview},
  created(){
    document.body.insertAdjacentHTML('afterbegin', icons)
  }
}
</script>

<style lang="scss">
  .page{
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: #EAEBEC;
    > .main {
      flex-grow: 1;
    }
    > .main {
      min-width: 1024px;
      max-width: 1440px;
      margin: 16px auto;
      display: flex;
      justify-content: space-between;
      padding: 8 16px;
      width: 100%;
    }
  }
  #Topbar {
    background: gold;
    box-shadow: 0 1px 3px rgba(0,0,0,.25);
    height: 64px;
  }
  #Editor{
    width: 35%;
    background: #444;
  }
  #Preview{
    flex-grow: 1;
    margin-left: 16px;
    background: #777;
    flex-grow: 1;
  }
  svg.icon {
    height: 1em;
    width: 1em;
    fill: currentColor;
    vertical-align: -0.1em;
    font-size: 16px;
  }
</style>



